<template>
	<view>
		<button @click='face_thread'>面部分析</button>
		<button>文本分析</button>
		<uni-easyinput></uni-easyinput>
		<button>绘画分析</button>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				face_analyse_timer: null,
				face_analyse_finish: true


			}
		},
		methods: {
			face_thread() {
				this.face_analyse_timer = setInterval(this.face_analyse, 3000)
			},
			face_analyse() {
				if (!this.face_analyse_finish) {
					return
				}
				this.face_analyse_finish = false;

				// #ifdef MP
				const camera_context = uni.createCameraContext();
				camera_context.takePhoto({
					quality: 'high',
					success: res => {
						const src = res.tempImagePath;
						const image_base64 = uni.getFileSystemManager().readFileSync(src, 'base64');
						const fetch_data = uni.request({
							url: 'localhost:8001/face_predict',
							method: 'post',
							data: image_base64,
							success: (res) => {
								console.log('请求成功', res);
								if (res.statusCode === 200) {
									// 处理成功响应
									console.log('响应数据', res.data);
								} else {
									// 处理错误响应
									console.error('错误状态码', res.statusCode);
								}
							},
							fail: (err) => {
								console.error('请求失败', err);
							}

						})
					}
				})
				// #endif
				uni.chooseImage({
					count: 1,
					sourceType: ['camera'], // 使用摄像头拍照
					success(res) {
						console.log('拍照成功', res.tempFilePaths[0]);
						// 处理拍照后的结果
					},
					fail(err) {
						console.error('拍照失败', err);
					}
				})

				this.face_analyse_finish = true;
			}
		},
	}
</script>

